import * as actionTypes from '../actions/actionTypes';
import {updateObject} from '../utility';
const initialState = {
    orders: [],
    loading:false,
    purchased: false
}
const purchaseBurgerStartHelper = (state,action) => {
    return updateObject(state,{
        loading:true
    })
}

const purchaseBurgerInitHelper = (state,action) => {
    return updateObject(state,{
        purchased:false
    })
}

const purchaseBurgerSuccessHelper = (state,action) => {
    const newOrder = updateObject(action.orderData,{
        id:action.orderId
    })
    return updateObject(state,{
        
        orders: action.orders.concat(newOrder),
        purchased: true,
        price:4
    });
}

const purchaseBurgerFailedHelper = (state,action) => {
    return updateObject(state,{
                
        loading: false
    })   
}

const fetchOrderStartHelper = (state,action) => {
    return updateObject(state,{
                    
        loading: true
    });
}

const fetchOrderSuccessHelper = (state,action) => {
    return updateObject(state,{
                        
        orders: action.orders,
        loading: false
    })

}

const fetchOrderFailedHelper = (state,action) => {
    console.log(state);
    console.log(action);
    return updateObject(state,{
                            
        loading: false
    })
}


const reducer = (state = initialState,action) => {
switch (action.type) {
    case actionTypes.PURCHASE_BURGER_START:
        return purchaseBurgerStartHelper(state,action)
        // return {
        //     ...state,
        //     loading:true
        // }
        case actionTypes.PURCHAGE_INIT:
            return purchaseBurgerInitHelper(state,action);
            // return {
            //     ...state,
            //     purchased:false
            // }
    case actionTypes.PURCHASE_BURGER_SUCCESS:return purchaseBurgerSuccessHelper(state,action);
        
        // const newOrder = {
        //     ...action.orderData,
        //     id:action.orderId
        // }
        // return {
        //     ...state,
        //     orders: action.orders.concat(newOrder),
        //     purchased: true,
        //     price:4
        // };
        case actionTypes.PURCHASE_BURGER_FAILED:
            return purchaseBurgerFailedHelper(state,action)

            case actionTypes.FETCH_ORDERS_START:
                return fetchOrderStartHelper(state,action);

                case actionTypes.FETCH_ORDERS_SUCCESS:
                    return fetchOrderSuccessHelper(state,action);

                    case actionTypes.FETCH_ORDERS_FAIL:
                        return fetchOrderFailedHelper(state,action);
    default:
        return state;
}
}

export default reducer;