import * as actionTypes from '../actions/actionTypes';
import {updateObject} from '../utility';
const initialState = {
    token: null,
    userId: null,
    error: null,
    loading: false,
    authRedirectPath:'/'
};


const authStart = (state, action) => {
return updateObject(state, {error:null, loading:true});
}

const authSuccess = (state,action) => {
    console.log(action);
    return updateObject(state, {error:null, loading:false, token: action.idToken,userId: action.userId});
}

const authFail = (state,action) => {
    return updateObject(state, {error:action.error, loading:true});
}

const reducer = (state=initialState, action) => {
    switch (action.type) {
        case actionTypes.AUTH_START:return authStart(state,action);
            case actionTypes.AUTH_SUCCESS:return authSuccess(state,action);
                case actionTypes.AUTH_FAIL:return authFail(state,action);
            case actionTypes.AUTH_LOGOUT: return authLogout(state, action);
            case actionTypes.SET_AUTH_REDIRECT_PATH: return setAuthRedirectPath(state, action);
    
        default:
            return state;
    }

}

const authLogout = (state, action) => {
    return updateObject(state, {state,   token: null, userId: null})
}
const setAuthRedirectPath = (state,action) => {
    return updateObject(state, {authRedirectPath:action.path})
}

export default reducer;