import React, {Component} from 'react';
import Aux from '../Aux/Aux';
import classes from './Layout.css';
import Toolbar from '../../components/Navigation/Toolbar/Toolbar';
import { connect } from 'react-redux';
import SideDrawer from '../../components/Navigation/SideDrawer/SideDrawer';
class Layout extends Component {
    state = {
        showSideDrawer: false
    }
    sideDrawerClosedHandler = () => {
        console.log('dfsf');
  this.setState({showSideDrawer:false});
    }

    drawerToggleClicked = () => {
        this.setState((prevState)=>{
        return {showSideDrawer: !prevState.showSideDrawer}
        });
    }
    render(){
        return (
        <Aux>
            <Toolbar isAuth={this.props.isAuthenticated} drawerToggleClicked={this.drawerToggleClicked}/>
        <SideDrawer isAuth={this.props.isAuthenticated}  open={this.state.showSideDrawer}
         modalClosed = {this.sideDrawerClosedHandler}/>

<main className={classes.Content}>
    {this.props.children}
</main>
    </Aux>

    
);
    }
} 
const mapStateToProps = state => {
    console.log('state is');
    console.log(state);
    return {
        isAuthenticated : state.auth.token != null
    }
}

export default connect(mapStateToProps)(Layout);