import * as actionTypes from '../actions/actionTypes';
import { updateObject} from '../utility';
const intialState = {
    ingredients: null,
    totalPrice:4,
    error: false,
    building:false
};
const INGREDIENT_PRICES = {
    salad:0.5,
    cheese:0.4,
    meat:1.3,
    bacon:0.7
};
const addIngredientHelper = (state,action)=>{

    const updatedIngredient = {[action.ingredientName]: state.ingredients[action.ingredientName] + 1};
    const updatedIngredients =  updateObject(state.ingredients,updatedIngredient);
    const  updatedState = {
        ingredients: updatedIngredients,
        totalPrice: state.totalPrice+INGREDIENT_PRICES[action.ingredientName],
        building:true
    }
     return updateObject(state, updatedState);
}

const removeIngredientHelper = (state,action)=>{
 
    const updatedIngredientt = {[action.ingredientName]: state.ingredients[action.ingredientName] - 1};
       const updatedIngredientss =  updateObject(state.ingredients,updatedIngredientt);
       const updatedStatee = {
           ingredients: updatedIngredientss,
           totalPrice: state.totalPrice-INGREDIENT_PRICES[action.ingredientName],
           building:true
       }
        return updateObject(state, updatedStatee);   
}

const setIngredientHelper = (state,action)=>{
    return updateObject(state,{
        ingredients: {
            salad: action.ingredients.salad,
            bacon: action.ingredients.bacon,
            cheese: action.ingredients.cheese,
            meat: action.ingredients.meat
        },
        error: false,
        totalPrice:4,
        building:false

     })
}


const setIngredientFailedHelper = (state,action)=>{
    
    return updateObject(state, {error:true})
}
const reducer = (state=intialState, action) => {
switch (action.type) {
    case actionTypes.ADD_INGREDIENT: return addIngredientHelper(state,action);
    case actionTypes.REMOVE_INGREDIENT: return removeIngredientHelper(state,action);
        //         return {
        //             ...state,
        //             ingredients:{
        //                 ...state.ingredients,
        //                 [action.ingredientName]: state.ingredients[action.ingredientName] - 1
        //             },

        //    totalPrice: state.totalPrice-INGREDIENT_PRICES[action.ingredientName]
        //          };


                 case actionTypes.SET_INGREDIENT:
                     return setIngredientHelper(state,action);
                    //  return {
                    //      ...state,
                    //      ingredients: action.ingredients,
                    //      error: false
                    //  }

                    // return {
                    //     ...state,
                    //     ingredients: {
                    //         salad: action.ingredients.salad,
                    //         bacon: action.ingredients.bacon,
                    //         cheese: action.ingredients.cheese,
                    //         meat: action.ingredients.meat
                    //     },
                    //     error: false
                    // }


                     case actionTypes.SET_INGREDIENT_FAILED:
                         return setIngredientFailedHelper(state, action);
                        // return {
                        //     ...state,
                        //     error: true
                        // }
        
        default:
            return state;

}}

export default reducer;